<template>
   <div class="userAssets">

      <div class="menmuTitle flex align between">
        <h1>资产详情&nbsp;&nbsp;<i class="el-icon-view" @click="showNum=!showNum" :class="showNum?'':'op5'"></i></h1>
        <div class="goLink pointer hoverTr3" @click="gopage('/userCenter/userAssetsList')">资产明细&nbsp;&nbsp;<i class="el-icon-right"></i></div>
      </div>
      <!-- menmuTitle -->

      <div class="listItem">

        <table>

          <thead>
            <tr>
              <th class="textLeft">资产代币</th>
              <th class="textLeft">可用额度</th>
              <!-- <th class="textCenter">锁仓额度</th> -->
              <th class="textLeft">资产折合 / USDT</th>
              <th class="textRight">操作</th>
            </tr>
          </thead>

          <tbody>
            <tr v-for="(item,index) in userInfo.balanceModels" :key="index">
              <td width="150">
                <div class="flex align icon"><i class="iconImg" :class="item.coin.coinName"></i> <span>{{item.coin.coinName}}</span></div>
              </td>
              <td class="textLeft">{{showNum ? item.amount : '--'}}</td>
              <!-- <td class="textCenter">{{showNum ? item.amount : '--'}}</td> -->
              <td class="textLeft">{{showNum? toFixed_num(item.amount * item.coin.coinPrice) : '--'}}</td>
              <td class="textRight"><div class="linkText"  @click="gopage(`/userCenter/userAssetsList?coinId=${item.coin.id}`)">查看明细</div></td>
            </tr>
          </tbody>
        </table>
      </div>
      <!-- assetsTable -->

  </div>
</template>

<script>
  import {
    mapMutations,
    mapState,
    mapActions
  } from 'vuex'
  
  
  export default {
    data() {
      return {
        showNum:true//显示资产
      }
    },
    computed: {
      ...mapState(['userInfo'])
    },
    methods: {

    },
    mounted() {
      
    } //mounted
  };

</script>
<style rel="stylesheet/scss" scoped lang="scss">
  @import "../styles/Val";
  td{
    width: 25%;
  }
  .op5{
    opacity: 0.5;
  }
  td{
    width: 20%;
  }
</style>


